@use 'base/variables';
@use 'base/mixins' as m;
@use 'base/utilities' as *;

.galeria{
    @include m.grid(1, 2rem);

    @include m.telefono { 
        @include m.grid(2,2rem);
     }
    @include m.desktop { 
        @include m.grid(4,1rem);
     }
    .galeria__imagen{
        img{
            margin: 0 auto;
            height: 30rem;
            width: 30rem;
            object-fit: cover;
        }
        @include m.desktop { 

            &:nth-child(1){
                grid-row: 1/3;
                img{
                    height: 62rem;
                    object-position: center;
                }
            }
            &:nth-child(2){
                grid-column: 2/4;
                img{
                    width: 62rem;
                }
            }
            &:nth-child(4){
                grid-row: 2/4;
                img{
                    height: 62rem;
                }
            }
            &:nth-child(5){
                grid-column: 3/5;
                img{
                    width: 62rem;
                }
            }
            &:nth-child(6){
                grid-column: 3/5;
                img{
                    width: 62rem;
                }
            }
            &:nth-child(7){
                grid-column: 1/2;
                grid-row: 3/5;
                img{
                    height: 62rem;
                }
            }
            &:nth-child(8){
                grid-column: 2/5;
                img{
                    width:94rem;
                }
            }
            &:nth-child(9){
                grid-column: 1/3;
                img{
                    width: 62rem;
                }
            }
            &:nth-child(10){
                grid-column: 3/5;
                img{
                    width: 62rem;
                }
            }
         }
    }
}